'use strict';

/**
 * Beacons store module
 * 
 * @author Victor Huerta <victor@compropago.com, vhuertahnz@gmail.com>
 */

import BeaconsService from './../../../lib/beacons/beacons_service';

const state = {
  pagination: {
    page: 1,
    totalPages: 1,
    total: 0,
    limit: 5,
    filters: {},
    records:  [],
    orderBy: 'created',
    order: 'desc'
  },
  controls: {
    loading: false,
    advancedFilters: false
  }
};

const getters = {
  page: state => state.pagination.page,
  totalPages: state => state.pagination.totalPages,
  total: state => state.pagination.total,
  limit: state => state.pagination.limit,
  filters: state => state.pagination.filters,
  records: state => state.pagination.records,
  order: state => state.pagination.order,
  orderBy: state => state.pagination.orderBy,
  showAdvancedFilters: state => state.controls.advancedFilters,
  isLoading: state => state.controls.loading
};

const mutations = {
  setOrder(state, order) {
    state.pagination.order = order;
  },
  setOrderBy(state, orderBy) {
    state.pagination.orderBy = orderBy;
  },
  setPage(state, page) {
    state.pagination.page = page;
  },
  setLimit(state, limit) {
    state.pagination.limit = limit;
    state.pagination.page = 1;
  },
  setFilter(state, payload) {
    state.pagination.filters = Object.assign({}, state.pagination.filters, payload);
  },
  toggleAdvancedFilters(state) {
    state.controls.advancedFilters = !state.controls.advancedFilters;
  },
  setLoading(state, payload) {
    state.controls.loading = payload;
  },
  found(state, payload) {
    state.pagination.totalPages = payload.totalPages;
    state.pagination.page = payload.page;
    state.pagination.total = payload.total;
    state.pagination.limit = payload.limit;
    state.pagination.records = payload.records;
  }
};

const actions = {
  find({ commit, getters, rootGetters }) {
    commit('setLoading', true);
    return BeaconsService.find(
        rootGetters['auth/companyId'],
        getters.filters,
        getters.page,
        getters.limit,
        getters.orderBy,
        getters.order
      )
      .then(response => {
        const { result, message } = response;
        commit('setLoading', false);
        commit('found', result);
      }).catch(err => {
        commit('setLoading', false);
        if(err && err.code && err.message) {
          commit('messages/error', { message: err.message, data: err.user }, { root: true });
        } else {
          commit('messages/error', 'error.remote', { root: true });
        }
        return Promise.reject(err);
      });
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};


