import qs from 'qs';

import config from './../config';
import AuthService from './../auth/auth_service';

const service = {

  init(endpoint, defaultHeaders, AuthService) {
    this.endpoint = endpoint;
    this.defaultHeaders = defaultHeaders;
    this.auth = AuthService;
    return this;
  },

  /**
   * Get a paginated beacons set
   * 
   * @param filters
   */
  find(companyId, filters, page, limit, orderBy, order) {
    const query = qs.stringify({
      page: page,
      order: order,
      orderBy: orderBy,
      limit: limit,
      filters: filters
    });

    const url = `${this.endpoint}/beacons/${companyId}?${query}`;
    const headers = Object.assign({}, this.defaultHeaders, this.auth.getAuthHeader());

    const options = {
      method: 'GET',
      headers
    };

    return fetch(url, options)
      .then(response => response.json())
      .then(json => json.code === 200 ? Promise.resolve(json) : Promise.reject(json));
  }
};

const instance = Object.create(service);

export default instance.init(config.api.endpoint, config.api.headers, AuthService);


