import queryString from 'query-string';

import Storage from './../util/storage_service';
import config from './../config';

const service = {
  init(endpoint, defaultHeaders) {
    this.endpoint = endpoint;
    this.defaultHeaders = defaultHeaders;

    return this;
  },

  getToken() {
    return Storage.get('auth.token');
  },

  getUser() {
    return Storage.get('auth.user');
  },

  login(email, password) {

    const url = `${this.endpoint}/auth/login`;

    const options = {
      method: 'POST',
      headers: Object.assign({}, this.defaultHeaders),
      body: JSON.stringify({ email, password })
    };

    return fetch(url, options)
      .then(response => response.json())
      .then(json => {

        if(json.code === 200) {
          if(json.token) {
            Storage.set('auth.token', json.token);
          }

          if(json.user) {
            Storage.set('auth.user', json.user);
          }
          return Promise.resolve(json);
        }

        return Promise.reject(json);
      });
  },

  passwordRecovery(email) {

    const url = `${this.endpoint}/auth/password_recovery`;

    const options = {
      method: 'POST',
      headers: Object.assign({}, this.defaultHeaders),
      body: JSON.stringify({ email })
    };

    return fetch(url, options)
      .then(response => response.json())
      .then(json => {
        if(json.code === 201) {
          return Promise.resolve(json);
        } else  {
          return Promise.reject(json);
        }
      });
  },

  verifyToken(token) {
    const url = `${this.endpoint}/auth/verify_token`;

    const options = {
      method: 'GET',
      headers: Object.assign({}, this.defaultHeaders, this.getAuthHeader()),
    };
    return fetch(url, options)
      .then(response => response.json())
      .then(json => {

        if(json.code === 200) {
          
          if(json.user) {
            Storage.set('auth.user', json.user);
          }
          return Promise.resolve(json);
        }

        return Promise.reject(json);

      });
  },

  isAuthenticated() {
    const token = this.getToken();
    return this.verifyToken(token);
  },

  getAuthHeader() {
    const token = this.getToken();
    return { 'X-Auth-Token': token }
  }

};

export default Object
  .create(service)
  .init(config.api.endpoint, config.api.headers);


