<style lang="sass">
  /* Enter and leave animations can use different */
  /* durations and timing functions.              */
  
  .slide-fade-enter-active {
    -webkit-transition: padding .15s ease, opacity 0.1s ease;
    -moz-transition: padding .15s ease, opacity 0.1s ease;
    -ms-transition: padding .15s ease, opacity 0.1s ease;
    -o-transition: padding .15s ease, opacity 0.1s ease;
    transition: padding .15s ease, opacity 0.1s ease;
  }
  
  .slide-fade-leave-active {
    -webkit-transition: padding .15s ease, opacity 0.20s ease;
    -moz-transition: padding .15s ease, opacity 0.20s ease;
    -ms-transition: padding .15s ease, opacity 0.20s ease;
    /* IE10 is actually unprefixed */
    -o-transition: padding .15s ease, opacity 0.20s ease;
    transition: padding .15s ease, opacity 0.20s ease;
  }
  
  .slide-fade-enter,
  .slide-fade-leave-to {
    max-height: 0;
    padding: 0;
    opacity: 0;
  }
  
  .slide-fade-enter-to,
  .slide-fade-leave {
    max-height: 1000px;
    opacity: 1;
  }
</style>
<template>
  <transition name="slide-fade">
      <div v-if="show" class="column is-12 notification is-marginless">
        <div class="container">
          <div class="columns is-multiline">
            <div class="column is-12">
              <h1 class="title is-4">
                {{$t(title)}}
                <a class="is-pulled-right button is-light" @click.prevent="close">
                  <i class="fa fa-angle-times fa-2x"></i>
                </a>
                <div class="is-clearfix"></div>
              </h1>
            </div>
            <slot></slot>
          </div>
        </div>
      </div>
  </transition>
</template>
<script>
  export default {

    name: 'AdvancedFilters',

    props: {
      show: {
        type: Boolean,
        required: true
      },
      title: {
        type: String,
        default: 'actions.advanced_search'
      }
    },

    methods: {
      close() {
        this.$emit('close', true);
      }
    }

  };
</script>


