<style lang="sass" scoped>
  div.beacon {
    &:hover {
      background-color: whitesmoke !important;
    }
    &:nth-child(odd) {
      background-color: #fafafa;
    }
  }
</style>

<template>
  <div class="columns is-marginless beacon">
    <div class="column is-1 has-text-centered is-light">
      <i class="fa fa-2x fa-podcast" :style="iconStyle(record)"></i>
    </div>
    <div class="column is-2">
      <p class="title is-6">
        {{record.name}}
      </p>
    </div>
    <div class="column is-4">
      <p class="title is-6 is-marginless">
        <strong>{{$t('beacons.fields.uuid')}}:</strong>&nbsp;&nbsp;{{record.uuid}}
        <br>
        <small>
          <strong>{{$t('beacons.fields.minor')}}:</strong>&nbsp;&nbsp;{{record.minor}}
        </small>
        <br>
        <small>
          <strong>{{$t('beacons.fields.major')}}:</strong>&nbsp;&nbsp;{{record.major}}
        </small>
      </p>
    </div>
    <div class="column is-2 has-text-centered">
      {{record.active? $t('adverb.yes') : $('adverb.no')}}
    </div>
    <div class="column is-3 has-text-right">

    </div>
  </div>
</template>
<script>
  export default {
    name: 'BeaconsGridRow',
    props: {
      record: {
        type: Object,
        required: true
      }
    },
    methods:  {
      iconStyle(beacon)  {
        return {
          color: beacon.color
        }
      }
    }
  }
</script>


