import Vue from 'vue';
import i18n from 'vue-i18n';

import es from './locales/es';
import ja from './locales/ja';

// ready translated locales
var locales = { es, ja };

// install plugin
Vue.use(i18n);

// set lang
Vue.config.lang = 'es';

// set locales
Object
  .keys(locales)
  .forEach(function (lang) {
    Vue.locale(lang, locales[lang]);
  });


