<style lang="sass" scoped>
  .dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    z-index: 1000;
    border-top-right-radius: 0;
  }
  
  .dropdown.is-open {
    display: block;
  }

</style>

<template>
    <div class="columns is-multiline is-marginless">
      <overlay :loading="isLoading"></overlay>

      <div class="column is-1 has-text-centered grid-header">
        <strong slot="content">
          {{$t('beacons.fields.color')}}
        </strong>
      </div>
      <div-sorteable 
        class="column is-2 grid-header"
        :field="'name'"
        :current="orderBy"
        :direction="order"
        @change="changeOrder">
        <strong slot="content">
          {{$t('beacons.fields.name')}}
        </strong>
      </div-sorteable>

      <div-sorteable 
        class="column is-4 grid-header"
        :field="'uuid'"
        :current="orderBy"
        :direction="order"
        @change="changeOrder">
        <strong slot="content">
          {{$t('beacons.fields.parameters')}}
        </strong>
      </div-sorteable>

      <div-sorteable 
        class="column is-2 has-text-centered grid-header"
        :field="'active'"
        :current="orderBy"
        :direction="order"
        @change="changeOrder">
        <strong slot="content">
          {{$t('beacons.fields.active')}}
        </strong>
      </div-sorteable>

      <div class="column is-3 has-text-centered grid-header">
        <strong>
          {{$t('fields.actions')}}
        </strong>
      </div>

      <div class="column is-12" v-if="!records.length">
        {{ $t('info.empty') }}
      </div>

      <beacon-grid-row class="column is-12 is-paddingless" v-if="records.length" v-for="record in records" :record="record">

      </beacon-grid-row>

    </div>

</template>

<script>
  import Overlay from './../common/Overlay';
  import DivSorteable from './../common/Grid/DivSorteable';
  import BeaconGridRow from './_BeaconsGridRow';

  export default {
    name: 'BeaconsGrid',

    components: {
      Overlay,
      DivSorteable,
      BeaconGridRow
    },
    computed:  {
      isEmpty() {
        return !this.records || !this.records.length;
      },
      records() {
        return this.$store.getters['beacons/records'];
      },
      order() {
        return this.$store.getters['beacons/order'];
      },
      orderBy() {
        return this.$store.getters['beacons/orderBy'];
      },
      isLoading() {
        return this.$store.getters['beacons/isLoading'];
      }
    },
    methods:  {
      changeOrder(field, direction)  {
        this.$store.commit('beacons/setPage', 1);
        this.$store.commit('beacons/setOrderBy', !!direction ? field : '');
        this.$store.commit('beacons/setOrder', direction);
        this.$store.dispatch('beacons/find');
      }
    }
  };
</script>


