import AuthService from './../../../lib/auth/auth_service';

const state = {
  email: '',
  password: '',
  loading: false,
  isLoading: state => state.loading
};

const getters = {
  email: state => state.email,
  password: state => state.password
};

const actions = {
  login({ commit, getters }) {
    commit('startLoading');
    return AuthService
      .login(getters.email, getters.password)
      .then(response => {
        commit('endLoading');
        commit('messages/success', { message: response.message, data: response.user }, { root: true });
        return Promise.resolve(response);
      })
      .catch(err => {
        commit('endLoading');
        if(err && err.code && err.message) {
          commit('messages/error', { message: err.message, data: err.user }, { root: true });
        } else {
          commit('messages/error', 'error.remote', { root: true });
        }
        return Promise.reject(err);
      });
  },

  passwordRecovery({ commit, getters }) {
    commit('startLoading');
    return AuthService
      .passwordRecovery(getters.email)
      .then(response => {
        commit('endLoading');
        commit('messages/success', response.message, { root: true });
        return Promise.resolve(response);
      })
      .catch(err => {
        commit('endLoading');
        if(err && err.code && err.message) {
          commit('messages/error', { message: err.message, data: err.user }, { root: true });
        } else {
          commit('messages/error', 'error.remote', { root: true });
        }
        return Promise.reject(err);
      });
  }
};

const mutations = {
  reset(state) {
    state.email = '';
    state.password = '';
    state.errors = undefined;
  },
  email(state, email) {
    state.email = email;
  },
  password(state, password) {
    state.password = password;
  },
  startLoading(state) {
    state.loading = true;
  },
  endLoading(state) {
    state.loading = false;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};


