<template>
<div class="main-wrapper section is-marginless is-paddingless">
  <div class="container is-marginless is-paddingless is-fluid">
    <navbar></navbar>
    <router-view></router-view>
  </div>
  <messages></messages>
</div>
</template>

<script>
  import Navbar from './../common/Navbar';
  import AuthService from './../../lib/auth/auth_service';
  import Messages from './../common/Messages.vue';

  export default {
    name: 'Secured',
    components:  {
      Navbar,
      Messages
    },
    beforeRouteEnter(to, from, next) {
      return AuthService.isAuthenticated()
        .then(() => next())
        .catch(() => next('/auth/login'));
    },
    created() {
      this.$store.dispatch('auth/loadUserData');
    },
    beforeDestroy() {
      this.$store.commit('auth/reset');
    },
    beforeRouteUpdate(to, from, next)  {
      this.$store.dispatch('auth/verifyToken')
        .then(res =>  this.$store.dispatch('auth/loadUserData'))
        .then(res => next())
        .catch(err => {
          next('auth/login');
        });
    }
  }
</script>


