'use strict';

/**
 * Export routes
 */

// import App from './components/layout/App.vue';
import Auth from './../components/layout/Auth.vue';
import PasswordRecovery from './../components/auth/PasswordRecovery.vue';
import Login from './../components/auth/Login.vue';

import Secured from './../components/layout/Secured.vue';
import BeaconsIndex from './../components/beacons/Index';
import PromosIndex from './../components/promos/Index';

export default [{
    path: '/',
    component: Secured,

    children: [{
        path: '/beacons',
        component: BeaconsIndex
      },
      {
        path: '/promos',
        component: PromosIndex
      }
    ],
  },
  {
    path: '/auth',
    component: Auth,
    children:  [{
        path: 'password_recovery',
        component: PasswordRecovery,
      },
      {
        path: 'login',
        component: Login,
      }
    ]
  }
];


