import AuthService from './../../../lib/auth/auth_service';

const state = {
  company: null,
  user: null,
  token: null,
  authenticated: false
};

const actions = {
  loadUserData({ commit, getters, state }) {
    const user = AuthService.getUser();
    const token = AuthService.getToken();

    if(!state.company || !user.companies.some(c => c._company.id === state.company.id)) {
      commit('setCompany', user.companies.shift()._company);
    }

    commit('setUser', user);
    commit('setToken', token);
  },

  verifyToken({ commit, getters }) {
    return getters['token'] ? AuthService
      .verifyToken(getters['token'])
      .then(response => {
        return Promise.resolve(response);
      })
      .catch(err => {
        if(err && err.code && err.message) {
          commit('messages/error', { message: err.message, data: err.user }, { root: true });
        } else {
          commit('messages/error', 'error.remote', { root: true });
        }
        return Promise.reject(err);
      }) : Promise.reject();
  }
};

const getters = {
  companyId: state => state.company && state.company.id,
  user: state => state.user,
  token: state => state.token,
  isAuthenticated: state => state.authenticated,
  isNotAuthenticated: state => !state.authenticated,
};

const mutations = {
  setAuthenticated(state, authenticated) {
    state.authenticated = authenticated;
  },
  setUser(state, user) {
    state.user = user;
  },
  setCompany(state, company) {
    state.company = company;
  },
  setToken(state, token) {
    state.token = token;
  },
  reset(state) {
    state.user = null;
    state.token = null;
    state.company = null;
    state.authenticated = false;
  }
};

export default  {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};


