<style lang="sass">
@media screen and (min-width: 769px) {
  .pagination-previous, .pagination-next {
    margin-left: 0;
  }
}
</style>

<template>
  <nav :class="['pagination', controllersClass]" v-if="total > 0">
    <a class="pagination-previous" @click.prevent="prev()"  v-if="total > 1">
      <slot name="prev">
        <
      </slot>
    </a>
    <a class="pagination-next" @click.prevent="next()"  v-if="total > 1">
      <slot name="next">
        >
      </slot>
    </a>
    <ul class="pagination-list">
      <li v-if="isFirst">
        <a class="pagination-link" @click.prevent="selected(1)">
          1
        </a>
      </li>
      <li v-if="firstPresent && total > 1">
        <span class="pagination-ellipsis">
          <slot name="ellipsis">...</slot>
        </span>
      </li>
      <li v-for="page in pages">
        <a :class="[active === page? 'is-current' : '', 'pagination-link']" @click.prevent="selected(page)">
          {{page}}
        </a>
      </li>
      <li v-if="!lastPresent && total > 1">
        <span class="pagination-ellipsis">
          <slot name="ellipsis">...</slot>
        </span>
      </li>
      <li v-if="!isLast">
        <a :class="[active === total? 'is-current' : '', 'pagination-link']" @click.prevent="selected(total)">
          {{total}}
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
  export default {
    props: {
      total: {
        type: Number,
        required: true,
        validator: (value) => value > -1
      },
      show: {
        type: Number,
        default: 5,
        validator: (value) => value > 2
      },
      current: {
        type: Number,
        required: true,
        validator: (value) => value > 0
      },
      controllers: {
        type: Boolean,
        default: true
      },
      controllersClass: {
        type: String,
        default: 'is-centered'
      }
    },
    methods: {
      prev() {
        const current = this.active > 1 ? this.active - 1 : 1;
        this.$emit('change', current);
      },
      selected(page) {
        this.$emit('change', page);
      },
      next() {
        const current = this.active < this.total ? this.active + 1 : this.total;
        this.$emit('change', current);
      }
    },
    computed: {
      active() {
        return this.current >= this.total ? this.total : this.current;
      },
      isFirst() {
        return this.pages.length && this.pages[0] !== 1;
      },
      firstPresent() {
        return this.pages.length && this.pages[0] > 2;
      },
      lastPresent() {
        return this.pages.length && this.pages[this.pages.length - 1] >= (this.total - 1);
      },
      isLast() {
        return this.pages.length && this.pages[this.pages.length - 1] === this.total;
      },
      pages() {
        let pages = [];
        let current = this.active;
        let show = this.show - 2;
        let range = (Math.floor((show) / 2));

        let start = (current - 1) - range;
        start = start < 0 ? 0 : start;
        let end = start + show;

        if(end - start === show && start === 0) {
          end = end + 1 <= this.total ? end + 1 : end;
        }

        if(this.total - start <= show) {
          start = (this.total - (this.show - 1));
          start = start < 0 ? 0 : start;
        }

        for(let i = 1; i < this.total; i++) {
          pages.push(i);
        }

        return pages.slice(start, end);

      }
    }
  }
</script>


