<template>
  <div class="columns is-marginless is-multiline">

    <filters>

      <div slot="left" class="field is-horizontal is-pulled-left">
        <div class="field-label is-normal">
          {{$t('fields.showing')}}
        </div>
        <beacons-limiter></beacons-limiter>
      </div>

      <div slot="right" class="is-pulled-right">
        <beacons-filters></beacons-filters>
      </div>

    </filters>

    <advanced-filters :show="showAdvancedFilters" @close="toggleAdvancedFilters">
      <beacons-advanced-filters></beacons-advanced-filters>
    </advanced-filters>

    <grid-wrapper>
      <beacons-grid slot="grid"></beacons-grid>
      <beacons-paginator slot="paginator" class="is-pulled-right"></beacons-paginator>
    </grid-wrapper>
  
  </div>
</template>

<script>
  import Filters from './../common/Grid/Filters';
  import AdvancedFilters from './../common/Grid/AdvancedFilters';
  import GridWrapper from './../common/Grid/GridWrapper';
  import Pagination from './../common/Grid/Pagination';
  import BeaconsPaginator from './_BeaconsPaginator';
  import BeaconsLimiter from './_BeaconsLimiter';
  import BeaconsFilters from './_BeaconsFilters';
  import BeaconsGrid from './_BeaconsGrid';
  import BeaconsAdvancedFilters from './_BeaconsAdvancedFilters';

  export default {
    name: 'BeaconsIndex',
    components: {
      Filters,
      GridWrapper,
      AdvancedFilters,
      BeaconsLimiter,
      BeaconsAdvancedFilters,
      BeaconsGrid,
      BeaconsPaginator,
      BeaconsFilters
    },
    mounted() {
      this.$store.dispatch('beacons/find');
    },
    computed: {
      showAdvancedFilters() {
        return this.$store.getters['beacons/showAdvancedFilters'];
      }
    },
    methods: {
      toggleAdvancedFilters()  {
        this.$store.commit('beacons/toggleAdvancedFilters')
      }
    }
  }
</script>


