<style lang="sass" scoped>
  div.pagination-wrapper {
    width: 300px;
  }
</style>

<template>  
  <div class="pagination-wrapper">
    <pagination 
      v-if="records.length"
      :total="total"
      :current="current"
      @change="changePage">

      <i class="fa fa-angle-left" slot="prev"></i>
      <i class="fa fa-angle-right" slot="next"></i>

    </pagination>
  </div>
</template>
<script>
  import Pagination from './../common/Grid/Pagination';

  export default {
    name: 'BeaconsPaginator',
    components: {
      Pagination
    },
    computed: {
      hasRecords() {
        return this.records && this.records.length;
      },
      records() {
        return this.$store.getters['beacons/records'];
      },
      current() {
        return this.$store.getters['beacons/page'];
      },
      total() {
        return this.$store.getters['beacons/totalPages'];
      }
    },
    methods: {
      changePage(page) {
        if(this.current !== page) {
          this.$store.commit('beacons/setPage', page);
          this.$store.dispatch('beacons/find');
        }
      }
    }
  }
</script>


