<template>
<div class="section">
  <div class="container">
    <div class="columns">
      <div class="column is-half is-offset-one-quarter box">
        
        <h1 class="title is-6 has-text-centered is-marginless">
          <i class="fa fa-3x fa-podcast">
          </i>
        </h1>
        
        <router-view></router-view>

      </div>
      <img class="bg" src="./../../assets/img/auth_bg.jpg" />
    </div>
    <div class="has-text-centered">
      <small>
      {{$t('auth.footer', {year})}}
      </small>
    </div>
    <messages></messages>
  </div>
</div>
</template>


<script>
  import Messages from './../common/Messages.vue';

  export default {
    name: 'Auth',
    components: {
      Messages
    },
    data() {
      return {
        year: new Date()
          .getFullYear()
      }
    }
  };
</script>

<style>
  img.bg {
    /* Set rules to fill background */
    min-height: 100%;
    min-width: 1024px;
    /* Set up proportionate scaling */
    width: 100%;
    height: auto;
    /* Set up positioning */
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
  }
  
  @media screen and (max-width: 1024px) {
    /* Specific to this particular image */
    img.bg {
      left: 50%;
      margin-left: -512px;
      /* 50% */
    }
  }
</style>


