<style lang="sass" scoped>
  @import './../../../assets/sass/mixins';

  div.sorteable {
    cursor: pointer;
    -webkit-touch-callout: none; /* iOS Safari */
    @include prefix('user-select', none);
  }

</style>

<template>

  <div @click="sort" class="sorteable">
    <slot name="content">
      {{field}}
    </slot>
    <slot name="sort" :is-orderd-by="isOrderedBy" :direction="direction">
      <i :class="[sortClass, 'is-pulled-right']"></i>
    </slot>
  </div>

</template>

<script>
  import Sorteable from './../Mixins/Sorteable'

  export default {
    mixins: [Sorteable]
  };
</script>


