<style scoped>
  div.error-wrapper span {
    font-size: .85em;
  }
</style>

<template>
  <div class="field">
    <slot :has-error="hasError()">
    </slot>
    <p class="help is-danger" v-show="hasError()" v-for="(message, key) in getErrors()">
      {{$t('error.field')}}, {{message}}
    </p>
  </div>
</template>
<script>
  import { get } from 'lodash';

  export default {
    props: {
      path: {
        type: String,
        required: true
      },
      showOnSubmitted: {
        type: Boolean,
        default: true
      },
      validator: {
        type: Object,
        required: true
      }
    },
    methods: {
      hasError() {
        return this.isSubmitted() && this.isError();
      },
      getErrors() {
        return get(this.validator, `$errors.${this.path}`)
      },
      isSubmitted() {
        return this.showOnSubmitted ? get(this.validator, '$submitted') : true;
      },
      isError() {
        return !!get(this.validator, `$errors.${this.path}`);
      }
    }
  };
</script>


