<style lang="sass">
  @import './../../../node_modules/bulma/sass/utilities/variables';

  .overlay {
    z-index:999; 
    background: transparentize($grey, 0.5);
    .sk-wave {
      margin: auto; 
      position: absolute; 
      top: 0; left: 0; bottom: 0; right: 0;
      .sk-rect {
        background: $grey-dark;
      }
    }
  }
</style>

<template>
  <div class="overlay is-overlay" v-if="show">
    <div class="sk-wave">
      <div class="sk-rect sk-rect1"></div>
      <div class="sk-rect sk-rect2"></div>
      <div class="sk-rect sk-rect3"></div>
      <div class="sk-rect sk-rect4"></div>
      <div class="sk-rect sk-rect5"></div>
    </div>
  </div>
</template>

<script>
  import {debounce} from 'lodash';

  export default {
    props: {
      loading: {
        type: Boolean,
        required: true
      }
    },
    data() {
      return {
        show: false
      }
    },
    watch: {
      isLoading(value) {
        if(value)
          this.setShow();
        else
          this.show = value;
      }
    },
    computed: {
      isLoading() {
        return this.loading;
      }
    },
    methods: {
      setShow: debounce(function() {
        this.show = this.loading;
      }, 200)
    }
  }
</script>


