const state = {
  icon: false,
  message: false,
  type: false
};

const getters = {
  icon: state => state.icon,
  message: state => state.message,
  type: state => state.type
};

const actions = {

};

const mutations = {
  message(state, message) {
    state.type = 'message';
    state.icon = 'cog';
    state.message = message;
  },
  warning(state, message) {
    state.type = 'warning';
    state.icon = 'exclamation';
    state.message = message;
  },
  error(state, message) {
    state.type = 'error';
    state.icon = 'times';
    state.message = message;
  },
  success(state, message) {
    state.type = 'success';
    state.icon = 'check';
    state.message = message;
  },
  reset(state, message) {
    state.type = false;
    state.icon = false;
    state.message = false;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};


