<template>
  <form v-on:submit.prevent="onSubmit">

    <h1 class="subtitle is-4 has-text-centered">
      {{$t('auth.recover.title')}}
    </h1>
    
    <validated-field :validator="validator" path="email">
      <template scope="props">
        <p class="control has-icon">
          <input :class="['input', {'is-danger': props.hasError}]" v-model="email" @input="validate" :placeholder="$t('auth.recover.fields.email')">
          <span class="icon">
            <i class="fa fa-envelope"></i>
          </span>
        </p>
      </template>
    </validated-field>

    <div class="field">
      <p class="control">
        <loader-wave  v-show="isLoading"></loader-wave>
        <button class="button is-primary is-fullwidth" type="submit" v-show="!isLoading">
          {{$t('auth.recover.fields.submit')}}
        </button>
      </p>
    </div>

    <p>
      {{$t('auth.recover.footer')}}
      <router-link :to="{ path: '/auth/login'}">
        {{$t('auth.login.title')}}
      </router-link>
    </p>
  </form>
</template>

<script>
  import jschemator from 'jschemator';
  import schemas from 'kb-schemas';
  import LoaderWave from './../common/LoaderWave.vue';
  import ValidatedField from './../common/ValidatedField.vue';

  export default {
    name: 'authForm',
    components: {
      ValidatedField,
      LoaderWave
    },
    data() {
      return {
        validator: jschemator(schemas.auth.passwordRecovery, this, 'es')
      }
    },
    computed: {
      email: {
        get() {
          return this.$store.getters['authForm/email'];
        },
        set(value)  {
          return this.$store.commit('authForm/email', value);
        }
      },
      isLoading() {
        return  this.$store.getters['authForm/isLoading'];
      }
    },
    beforeMount(){
      return this.$store.commit('authForm/reset');
    },
    methods:  {
      validate()  {
        return this.validator.validate();
      },
      onSubmit(algo) {
        const _this = this;
        if(_this.validator.validate(true)) {

          _this.$store
            .dispatch('authForm/passwordRecovery')
            .then(response => {
              _this.validator.set(!response.errors, response.errors)
              if(response.code === 201) {
                _this.$router.push('/auth/login');
                return _this.$store.commit('authForm/reset', value);
              }
            })
            .catch(err => err /* Already catched */ );
        } else {
          _this.$store.commit('messages/error', 'error.verify')
        }
      }
    }
  };
</script>


