<template>  
  <span class="select">
    <select  v-model="limit">
      <option v-for="n in numbers" :value="n">{{n}}</option>
    </select>
  </span>
</template>
<script>

  export default {
    name: 'BeaconsLimiter',
    data() {
      return {
        numbers: [5, 10, 30, 50, 100]
      }
    },
    computed: {
      limit: {
        set(value) {
          this.$store.commit('beacons/setLimit', value);
          this.$store.dispatch('beacons/find');
        },
        get() {
          return this.$store.getters['beacons/limit'];
        }
      }
    }
  }

</script>


