<template>
  <div class="field has-addons">
    <p class="control has-icon has-icon-right is-expanded">
      <input class="input" type="text" :placeholder="$t('actions.search')" v-model="general">
      <span class="icon is-small">
        <i class="fa fa-search"></i>
      </span>
    </p>
    <p class="control">
      <a :class="['button', {'is-primary is-outlined': showAdvancedFilters}]" @click="toggleAdvancedFilters">
        <i class="fa fa-sliders"></i>
      </a>
    </p>
  </div>
</template>
<script>
  import { debounce } from 'lodash';

  export default  {
    name: 'BeaconsFilters',
    computed: {
      general: {
        set(value)  {
          this.$store.commit('beacons/setFilter', { 'general': value });
        },
        get() {
          return this.$store.getters['beacons/filters'].general;
        }
      },
      showAdvancedFilters() {
        return this.$store.getters['beacons/showAdvancedFilters'];
      }
    },
    watch: {
      general() {
        this.find();
      }
    },
    methods: {
      find: debounce(function () {
        this.$store.dispatch('beacons/find')
      }, 300),

      toggleAdvancedFilters()  {
        this.$store.commit('beacons/toggleAdvancedFilters')
      }
    },

  }
</script>


