<script>
  import alertify from 'alertifyjs';

  alertify.set('notifier', 'position', 'top-right');

  export default {

    mounted() {
      const _this = this;

      _this.$store.watch(
        () => _this.$store.getters['messages/message'],
        (message) => {
          if(message) {
            let formatted;
            if(typeof message === 'string') {
              formatted = _this.$t(message);
            } else {
              formatted = _this.$t(message.message, message.data);
            }

            alertify.notify(
              `
                <i class="fa fa-lg fa-${_this.$store.getters['messages/icon']}"></i>&nbsp;&nbsp;&nbsp;
                ${formatted}
              `,
              _this.$store.getters['messages/type']
            );
            _this.$store.commit('messages/reset');
          }
        }
      );
    }
  }
</script>

<style>
  @import './../../../node_modules/alertifyjs/build/css/alertify.min.css';
  @import './../../../node_modules/alertifyjs/build/css/themes/bootstrap.min.css';
  .alertify-notifier .ajs-message.ajs-error,
  .alertify-notifier .ajs-message.ajs-success,
  .alertify-notifier .ajs-message.ajs-warning,
  .alertify-notifier .ajs-message.ajs-message {
    text-align: left;
    text-shadow: none;
    border: none;
  }
  
  .alertify-notifier .ajs-message.ajs-message {
    background: #95a5a6;
    color: #ffffff;
  }
  
  .alertify-notifier .ajs-message.ajs-warning {
    background: #f39c12;
  }
  
  .alertify-notifier .ajs-message.ajs-error {
    background: #e74c3c;
  }
  
  .alertify-notifier .ajs-message.ajs-success {
    background: #18bc9c;
  }
</style>


