<script>

  const orderType = ['asc', 'desc', ''];

  export default {
    props: {
      field: {
        type: String,
        required: true
      },
      current: {
        type: String,
        required: true
      },
      direction: {
        type: String,
        required: true,
        validator: value => orderType.indexOf(value.toLowerCase()) > -1
      },
      ascClass: {
        type: String,
        default: 'fa fa-sort-desc'
      },
      descClass: {
        type: String,
        default: 'fa fa-sort-asc'
      },
      unsortedClass: {
        type: String,
        default: 'fa fa-sort'
      }
    },
    computed: {
      sortClass() {
        if(this.isOrderedBy)
          return this.direction === 'asc'? this.ascClass : this.descClass;
        else
          return this.unsortedClass;
      },
      isOrderedBy() {
        return this.field === this.current;
      }
    },
    methods: {
      sort() {
        let order = 'asc';
        if(this.isOrderedBy) {
          switch(this.direction) {
          case 'asc':
            order = 'desc';
            break;
          case 'desc':
            order = '';
            break;
          }
        }
        this.$emit('change', this.field, order)
      }
    }
  }

</script>


