<template>
  <div>
    <form v-on:submit.prevent="onSubmit">

      <h1 class="subtitle is-4 has-text-centered">
        {{$t('auth.login.title')}}
      </h1>

      <validated-field :validator="validator" path="email">
        <template scope="props">
          <p class="control has-icon">
            <input :class="['input', {'is-danger': props.hasError}]" v-model="email" @input="validate" :placeholder="$t('auth.login.fields.email')">
            <span class="icon">
              <i class="fa fa-envelope"></i>
            </span>
          </p>
        </template>
      </validated-field>

      <validated-field :validator="validator" path="password">
        <template scope="props">
          <p class="control has-icon">
            <input :class="['input', {'is-danger': props.hasError}]" type="password" v-model="password" @input="validate"  :placeholder="$t('auth.login.fields.password')">
            <span class="icon">
              <i class="fa fa-key"></i>
            </span>
          </p>
        </template>
      </validated-field>

      <div class="field">
        <p class="control">
          <loader-wave  v-show="isLoading"></loader-wave>
          <button class="button is-primary is-fullwidth" type="submit" v-show="!isLoading">
            {{$t('auth.login.fields.submit')}}
          </button>
        </p>
      </div>
      
      <p>
        {{$t('auth.login.footer')}}
        <router-link :to="{ path: '/auth/password_recovery'}">
          {{$t('auth.recover.title')}}
        </router-link>
      </p>
      
    </form>
  </div>
  
</template>

<script>
  import jschemator from 'jschemator';
  import schemas from 'kb-schemas';
  import ValidatedField from './../common/ValidatedField.vue';
  import LoaderWave from './../common/LoaderWave.vue';

  export default {
    name: 'authForm',
    components: {
      ValidatedField,
      LoaderWave
    },
    data() {
      return {
        validator: jschemator(schemas.auth.login, this, 'es')
      }
    },
    computed: {
      email: {
        get() {
          return this.$store.getters['authForm/email'];
        },
        set(value)  {
          return this.$store.commit('authForm/email', value);
        }
      },
      password: {
        get() {
          return this.$store.getters['authForm/password'];
        },
        set(value)  {
          return this.$store.commit('authForm/password', value);
        }
      },
      isLoading() {
        return  this.$store.getters['authForm/isLoading'];
      }
    },
    beforeMount() {
      return this.$store.commit('authForm/reset');
    },
    beforeDestroy() {
      return this.$store.commit('authForm/reset');
    },
    methods:  {
      validate()  {
        return this.validator.validate();
      },
      onSubmit() {
        const _this = this;
        if(_this.validator.validate(true)) {
          _this.$store
            .dispatch('authForm/login')
            .then(res => _this.$router.push('/beacons'))
            .catch(err => err && err.errors && _this.validator.set(!response.errors, response.errors));
        } else {
          _this.$store.commit('messages/error', 'error.verify')
        }
      }
    }

  }
</script>


